@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
*, *::before, *::after {
  margin: 0;
  box-sizing: border-box;
}
body {
  min-height: 100vh;
  background: rgb(221,214,243);
  background: linear-gradient(0deg, rgba(221,214,243,1) 0%, rgba(255,255,255,1) 100%);
}

.feed {
    min-height: 80vh;
    width: 1000px;
    padding: 12.5px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 12.5px;
    gap: 12.5px;
}
.main__container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.post-container {
    width: 750px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 12.5px;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(101, 101, 101, 0.5);
    border-radius: 12.5px;
}
.post-container:hover {
    background: rgba(255, 255, 255, 0.5);
}
.profile-picture {
    min-width: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.profile-picture--container {
    background-color: white;
    border-radius: 50%;
    width: 75px;
    height: 75px;
}
.profile-picture--container img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.profile-picture--container img:hover {
    cursor: pointer;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
}
.post {
    width: 100%;
    padding-right: 12.5px;
    font-size: 16px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.header-info{
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: baseline;
            align-items: baseline;
    grid-gap: 5px;
    gap: 5px;
    width: 100%;
    font-family: 'Poppins', sans-serif;
}
.header-info h3 {
    font-size: 16px;
}
.header-info h3:hover {
    text-decoration: underline;
    cursor: pointer;
}
.header-info p {
    font-family: 'Ubuntu', sans-serif;
    font-size: 14px;
    color: rgb(101, 101, 101);
}
#verified-icon {
    width: 15px;
    height: 15px;
    -webkit-align-self: center;
            align-self: center;
}
#navigate-arrow {
    position: absolute;
    right: 0;
    width: 15px;
    height: 15px;
    -webkit-align-self: center;
            align-self: center;
    fill: rgb(101, 101, 101);
}
#navigate-arrow:hover {
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.3s linear;
}
.content {
    font-family: 'Poppins', sans-serif;
    margin: 5px 0;
    width: 100%;
}
.post-image {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    min-height: 100px;
    width: 100%;
    margin: 10px 0;
}
.post-image img {
    border-radius: 15px;
    max-width: 100%; 
    height: auto; 
    display: block;
}
.post-image img:hover {
    cursor: pointer;
}
.post-activity {
    width: 100%;
    font-family: 'Ubuntu', sans-serif;
    font-size: 14px;
    color: rgb(101, 101, 101);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-bottom: 10px;
}
.likes, .comments, .reposts {
    display: -webkit-flex;
    display: flex;
    grid-gap: 7.5px;
    gap: 7.5px;
    -webkit-align-items: center;
            align-items: center;
}
#like, #comment, #repost {
    width: 20px;
    height: auto;
    transition: all 0.3s ease-in-out;
}
#share {
    width: 18px;
    height: auto;
    transition: all 0.3s ease-in-out;
}
#like:hover, #comment:hover, #repost:hover, #share:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}
.like__button, .comment__button, .repost__button {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: transparent;
    border: none;
    outline: none;
}
#comment {
    position: relative;
    top: 2px;
}
.header {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.inputField {
    padding: 10px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 100%;
}
.authorSelect__container, .textSelect__container, .photoSelect__container, .submitButton__container {
    grid-gap: 10px;
    gap: 10px;
    width: 22%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.authorSelect__header, .textSelect__header, .photoSelect__header, .submitButton__header {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 16px;
}
.authorSelect, .inputField__input, .submitButton {
    background-color: transparent;
    border: 1px solid rgba(128, 128, 128, 1);
    box-shadow: 5px 5px 5px 2px rgba(128, 128, 128, 0.5);
    padding: 10px 10px;
    width: 100%;
    transition: all 0.3s linear;
}
.authorSelect, .inputField__input, .submitButton, .authorSelect option {
    font-size: 14px;
    font-family: 'Ubuntu', sans-serif;
}
.submitButton:hover {
    cursor: pointer;
}
.submitButton:active {
    box-shadow: none;
}
.error__container {
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 16px;
    font-family: 'Ubuntu', sans-serif;
    text-align: center;
    font-weight: 500;
    color: #FF3636;
    width: 100%;
    min-height: 25px;
}
